import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getOrders, getOrderMessages, sendMessage } from './actions';
import moment from 'moment';
import Loader from 'react-loader';
import './App.css';
import Input from './Input';

import Navbar from './Navbar';

class App extends Component {
    state = {
        limit: 10,
        openedOrders: []
    };

    componentDidMount() {
        this.getOrders();
    }

    getOrders(query) {
        if (query) {
            return this.props.dispatch( getOrders(query) );
        }

        const { limit } = this.state;

        this.props.dispatch( getOrders({
            filter: {
                limit
            }
        }) );
    }

    onOrderOpen(orderId) {
        let { openedOrders } = this.state;
        openedOrders.push(orderId);

        this.props.dispatch( getOrderMessages(orderId) );

        this.setState({ openedOrders });
    }

    onOrderClose(orderId) {
        let { openedOrders } = this.state;
        const orderIndex = openedOrders.indexOf(orderId);
        openedOrders.splice(orderIndex, 1);

        this.setState({ openedOrders });
    }

    getMoreOrders() {
        let { limit } = this.state;
        limit += 10;
        this.setState({ limit });

        this.getOrders({
            filter: {
                limit
            }
        })
    }

    sendMessage(id, message) {
        this.props.dispatch( sendMessage(id, message) );
    }

    renderOrders() {
        const { orders, messages, messagesFetchStatus } = this.props.app;
        const { openedOrders } = this.state;

        return orders.map((order, index) => {
            const orderId = order.id;
            const orderDate = moment(order.createdAt).format("dddd, MMMM Do YYYY, h:mm A");

            const isOpened = openedOrders.find((id) => {
                return orderId === id;
            });

            let orderMessages = messages.find((msgObj) => {
                return orderId === msgObj.id;
            });

            const msgBubbleClass = (index % 2 === 0) ? 'msg-bubble-white' : '';

            if (isOpened) {
                return (
                    <tr key={index}>
                        <td colSpan="5">
                            <div>
                                <div className="clearfix">
                                    <h3 className="pull-left">Order #{orderId}</h3>

                                    <button className="btn btn-danger pull-right" onClick={this.onOrderClose.bind(this, orderId)}>X</button>
                                </div>

                                <hr />

                                <div className="col-sm-4">
                                    <p><strong>ID: </strong>{orderId}</p>
                                    <p><strong>Title: </strong>{order.title}</p>
                                    <p><strong>Status: </strong>{order.status}</p>
                                    <p><strong>Price: </strong> <span>${order.price}</span></p>
                                </div>

                                <div className="col-sm-8">
                                    <p><strong>Task</strong></p>

                                    <p>{order.task}</p>
                                </div>

                                <div className="clearfix" />

                                <div>
                                    {messagesFetchStatus === 'LOADING' && <p>Loading messages</p>}

                                    {messagesFetchStatus === 'SUCCESS' && orderMessages.messages.map((msg, msgIndex) => {
                                        const isClient = msg.by === 'client';

                                        return (
                                            <div key={msgIndex} className="clearfix">
                                                <div className={`${msgBubbleClass} msg-bubble ${isClient ? 'client' : 'support'}`}>
                                                    <div className="row">
                                                        <div className="col-sm-4"><strong>{msgIndex + 1}. {isClient ? 'Client' : 'Support'}</strong></div>
                                                        <div className="col-sm-8 text-right">{orderDate}</div>
                                                    </div>

                                                    <hr />

                                                    <div className="row">
                                                        <div className="col-sm-12">
                                                            <p>{msg.text}</p>
                                                        </div>
                                                    </div>

                                                    <div className={isClient ? 'msg-triangle-client' : 'msg-triangle-support'} />
                                                </div>
                                            </div>
                                        );
                                    })}

                                    <Input sendMessage={this.sendMessage.bind(this)} id={orderId} />
                                </div>
                            </div>

                            <div className="clearfix" />
                        </td>
                    </tr>
                );
            }

            return (
                <tr key={index} onClick={this.onOrderOpen.bind(this, orderId)} className="order">
                    <td>{orderId}</td>
                    <td>{order.title}</td>
                    <td>{orderDate}</td>
                    <td>${order.price}</td>
                    <td>{order.status}</td>
                </tr>
            )
        });
    }

    render() {
        const { ordersFetchStatus } = this.props.app;

        return (
            <div>
                <Navbar />

                { ordersFetchStatus === 'LOADING' && <Loader loaded={false} /> }

                <div className="container">
                    <div className="table-responsive">
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <td>ID</td>
                                    <td>Order title</td>
                                    <td>Placement date</td>
                                    <td>Price</td>
                                    <td>Status</td>
                                </tr>
                            </thead>

                            <tbody>
                                {this.renderOrders()}
                            </tbody>
                        </table>
                    </div>

                    { ordersFetchStatus === 'SUCCESS' && <button className="btn btn-primary center-block" onClick={this.getMoreOrders.bind(this)}>Get more orders</button>}
                </div>
            </div>
        );
    }
}

App = connect(state => state)(App);

export default App;


