import apiClass from '../api';

const api = new apiClass();

export const GET_ORDERS = 'GET_ORDERS';
export const GET_ORDERS_SUCCESS = 'GET_ORDERS_SUCCESS';

export const GET_ORDER_MESSAGES = 'GET_ORDER_MESSAGES';
export const GET_ORDER_MESSAGES_SUCCESS = 'GET_ORDER_MESSAGES_SUCCESS';

export const SEND_MESSAGE_SUCCESS = 'SEND_MESSAGE_SUCCESS';

export function getOrders(query) {
    return (dispatch) => {
        dispatch({ type: GET_ORDERS });

        return api.getOrders(query).then((orders) => {
            dispatch({ type: GET_ORDERS_SUCCESS, orders });
        });
    }
}

export function getOrderMessages(orderId) {
    return (dispatch) => {
        dispatch({ type: GET_ORDER_MESSAGES });
        return api.getOrderMessages(orderId).then((orderMessages) => {
            dispatch({ type: GET_ORDER_MESSAGES_SUCCESS, orderId, orderMessages });
        });
    }
}

export function sendMessage(id, message) {
    return (dispatch) => {
        return api.sendMessage(id, message).then((message) => {
            dispatch({ type: SEND_MESSAGE_SUCCESS, message, id });
        });
    }
}



