import * as types from '../actions';

let initialState = {
    orders: [],
    messages: [],
    ordersFetchStatus: 'LOADING',
    messagesFetchStatus: 'LOADING'
};

export default function app(state = initialState, action) {
    switch(action.type) {
        case types.GET_ORDERS:
            return {
                ...state,
                ordersFetchStatus: 'LOADING'
            };

        case types.GET_ORDERS_SUCCESS:
            return {
                ...state,
                orders: action.orders,
                ordersFetchStatus: 'SUCCESS'
            };

        case types.GET_ORDER_MESSAGES:
            return {
                ...state,
                messagesFetchStatus: 'LOADING'
            };

        case types.GET_ORDER_MESSAGES_SUCCESS:
            const orderMessages = {
                id: action.orderId,
                messages: action.orderMessages
            };

            let newOrderMessages = state.messages;

            newOrderMessages.push(orderMessages);

            return {
                ...state,
                messages: newOrderMessages,
                messagesFetchStatus: 'SUCCESS'
            };

        case types.SEND_MESSAGE_SUCCESS:
            let messages = state.messages;

            let currentMessages = messages.find((msgObj) => {
                return msgObj.id === action.id;
            });

            currentMessages.messages.push(action.message);

            messages.filter((obj) => {
                return obj.id === action.id;
            });

            messages.push(currentMessages);

            return {
                ...state,
                messages
            }

        default:
            return state;
    }
}


