import request from 'superagent';

const apiUrl = 'http://sample-env.py8izi2xbh.us-west-2.elasticbeanstalk.com/api';

class api {
    getOrders(query) {
        return this._request('/Orders', 'GET', {}, query);
    }

    getOrderMessages(id) {
        return this._request(`/Orders/${id}/messages`, 'GET');
    }

    sendMessage(id, message) {
        return this._request(`/Orders/${id}/messages`, 'POST', { text: message});
    }

    _request(resource, method = 'GET', params = {}, query = {}) {
        if (!resource) {
            throw new Error('Resource is required for request');
        }

        const url = `${apiUrl}${resource}`;

        return new Promise((resolve, reject) => {
            request[method.toLowerCase()](url)
                .query(query)
                .send(params)
                .end((err, res) => {
                    if (err) {
                        return reject(res && res.body ? res.body.errors : err, err, res);
                    }
                    return resolve(res.body, res);
                });
        });
    }
}

export default api;


